ul {
  padding-inline-start: 0px;
}
body {
  background-color: #d6ccc2;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  overflow-x: hidden;
  margin: auto;
}
.App {
  max-width: 1080px;
  overflow-x: hidden;
  margin: auto;
}

div.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: auto;
  min-height: 150px;

  background-color: #edede9;
}

header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
  background-color: #fff;
  
}
reach ul,
li {
  list-style: none;
  padding: 4px;
  
}
div.reach {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  text-align: center;
  margin: auto;
  font-family: europa, sans-serif;
  font-weight: 300;
  font-style: normal;
  background-color: #edede9;
  overflow-x: hidden;
  font-size: 14px;
  text-transform: uppercase;
}

div.contact {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: auto;
  background-color: #fff;
  overflow-x: hidden;
  
}
.navcontact {
  display: flex;
  flex-direction: column;
  list-style: none;
  padding: 15px;
  margin: auto;
}

.navcontact li {
  display: inline-block;
  list-style: none;
  padding: 0px 10px;

}

.navcontact li a {
  transition: all 0.3s ease 0s;
  font-family: kepler-std-display, serif;
  font-weight: 300;
  font-style: normal;
  font-size: 17px;
  text-decoration: none;
  color: black;
}

.navcontact li a:hover {
  color: #d5bdaf;
}

.rescontainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  background-color: #fff;
  /* margin: 0 150px 0px 150px; */
}

.edwork {
  margin: 20px;
}

.edwork li {
  list-style: none;
  list-style-type: none;
  padding: 0px 10px;
  text-decoration: none;
  line-height: 1.5;
  margin-left: 0px;
  text-align: center;
  font-family: europa, sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 14px;
}

.aboutme {
  margin: 20px;
}
.careergoal {
  margin: 20px;
}

.rescontainer-2 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  background: #fff;
  /* margin: 0 150px 0px 150px; */
}
.aboutme {
  width: 300px;
}
.careergoal {
  width: 300px;
}

.skills {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

p {
  line-height: 1.5;
  text-align: center;
  font-family: europa, sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 14px;
}
h1 {
  margin: 10px;
  text-align: center;
  font-family: kepler-std-display, serif;
  font-weight: 300;
  font-size: 54px;
  font-style: normal;
  line-height: 1em;
  text-transform: capitalize;
}
h2 {
  margin: auto;
  text-transform: uppercase;
  text-align: center;
  font-family: kepler-std-display, serif;
  font-style: normal;
  font-weight: 300;
  font-size: 25 px;
  letter-spacing: 0.02em;
  line-height: 1.4em;
  margin: 10px;
}
.skills li {
  list-style: none;
  list-style-type: none;
  padding: 0px 10px;
  text-decoration: none;
  line-height: 1.5;
  margin-left: 0px;
  text-align: center;
  font-family: europa, sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 14px;

}

.skills {
  background: #fff;
}
@media (min-width: 511px) {
  header {
    flex-direction: row;
  }
}
